@import url(https://fonts.googleapis.com/css?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family:'Varela Round', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.Main {
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #fafafa;
  font-family: "Open Sans", sans-serif;
  margin-top: 8%;
}

.secondHalf {
  background-color: white;
  color: #333333;
  float: right;
  margin-right: 5px;
  margin-top: 5px;
  border-radius: 8px;
}

/* .firstHalf{
  display: flex;
  justify-content: center;
  align-items: center;
  
} */

.button {
  background-color: #4caf50; /* Green */
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 26px;
  margin: 4px 2px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
  margin-left: 65%;
  margin-top: 20%;
}

.firstButton {
  background-color: white;
  color: black;
  border: 2px solid #68e1fd;
  border-radius: 5px;
}

.firstButton:hover {
  background-color: #68e1fd;
  color: white;
}

.title {
  font-size: 3em;
  margin-bottom: 0px;
}

.tagline {
  font-size: 5em;
  margin: 0px;
}

.logo {
  height: auto;
  width: 90%;
}

.MainApp {
    background-image: url(/images/blob-shape2.svg), none;
    background-repeat: no-repeat;
    background-size:90%;
    background-position:center;
    margin: 40px;
    display: flex;
    justify-content: space-evenly;
}
.InfoCard {
  width: 700px;
  height: 600px;
  position: relative;
  overflow: hidden;
  border-radius: 18px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
}

.Alignment {
  margin: 50px;
}

.Alignment h2 {
  margin-top: 0px;
  color:#333333;
}

.Alignment p,
li {
  font-size: 20px;
}

.InfoNav {
  display: flex;
  justify-content: space-between;

  font-size: 20px;
}

.left {
  color: #9f9f9f;
  cursor: pointer;
  align-self: flex-end;
}

.right {
  font-weight: bold;
  cursor: pointer;
  color: #585858;
  float:right;
}

.Card {
  width: 420px;
  height: 700px;
  position: relative;
  border-radius: 18px;
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top:7px;
  background-color:white;
  z-index: 100;
}

.Card img {
  width: 300px;
  height: 300px;
  border-radius: 18px;
}

.Card h2 {
  font-size: 36px;
  margin-bottom: 0px;
}

.Card p {
  color: #6a6a6a;
  font-size: 20px;
  margin-top: 0px;
  font-weight: bold;
}

.Card p.Good {
  color: #4caf50;
}

.Card p.Bad {
  color: #ff5454;
}

#initiator {
  background-color: #00D084; /* Green */
  height: 40px;
  width: 320px;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 22px;
  margin: 4px 20px;
  cursor: pointer;
  border-radius: 8px;
  padding-top:20px;
  
}

.Card video {
  border-radius: 20px;
}

.videoContainer {
  position: relative;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
}

#notwanted {
  display: none;
}

